export default {
	data() {
		return {
			navs: ['全部', '待付款', '待发货', '待收货', '已收货', '已完成'],
			currentIndex: 0,
			orders: [],
			page: 1,
			isNull: false,
			stopLoad: false,
			modelName: '',
			modelIndex: null,
			tipTxt: '',
			isLoading: true,
			showBtn: 0
		}
	},
	onLoad(options) {
		console.log(options)
		this.currentIndex = parseFloat(options.status) + 1
	},
	onShow() {
		this.checkType()
	},
	methods: {
		chooseOptions(index) {
			this.currentIndex = index
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
			this.checkType()
		},
		//判断订单类型
		checkType() {
			this.page = 1
			this.isLoading = true
			this.isNull = this.stopLoad = false
			let type
			switch (this.currentIndex) {
				case 0:
					type = ''
					break;
				case 1:
					type = 'WAITPAY'
					break;
				case 2:
					type = 'WAITDELIVER'
					this.getAftersaleBtn()
					break;
				case 3:
					type = 'WAITRECEIVE'
					break;
				case 4:
					type = 'HASRECEIVED'
					this.getAftersaleBtn()
					break;
				case 5:
					type = 'FINISHED'
					break;
			}
			this.getOrderList(type)
		},
		//获取订单列表
		getOrderList(type) {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderList",
				data: {
					token: uni.getStorageSync('token'),
					type: type,
					page: this.page,
					pagesize: 6
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.orders = res.data.data
						this.isLoading = false
						if (this.orders.length == 0) {
							this.isNull = true
							return
						}
						for (let i = 0; i < this.orders.length; i++) {
							let totalNum = 0
							for (let j = 0; j < this.orders[i].list.length; j++) {
								totalNum += parseFloat(this.orders[i].list[j].goods_num)
							}
							this.orders[i].total_num = totalNum
						}
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//加载更多订单
		loadmore() {
			if (this.stopLoad) {
				return
			}
			this.page++
			let type
			switch (this.currentIndex) {
				case 0:
					type = ''
					break;
				case 1:
					type = 'WAITPAY'
					break;
				case 2:
					type = 'WAITDELIVER'
					break;
				case 3:
					type = 'WAITRECEIVE'
					break;
				case 4:
					type = 'HASRECEIVED'
					break;
				case 5:
					type = 'FINISHED'
					break;
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderList",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					type: type,
					page: this.page,
					pagesize: 6
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.data.length == 0) {
						this.stopLoad = true
						return
					}
					for (let i = 0; i < res.data.data.length; i++) {
						let totalNum = 0
						for (let j = 0; j < res.data.data[i].list.length; j++) {
							totalNum += parseFloat(res.data.data[i].list[j].goods_num)
						}
						res.data.data[i].total_num = totalNum
						this.orders.push(res.data.data[i])
					}
					console.log(this.orders)
				}
			})
		},
		//获取后台状态是否显示售后
		getAftersaleBtn() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/check_shouhou",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.showBtn = res.data.status
				}
			})
		},
		//删除订单
		sureDel() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderDelete",
				data: {
					order_id: this.orders[this.modelIndex].order_id,
					shop_id: uni.getStorageSync('shop_id'),
					token: uni.getStorageSync('token')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					this.closeModel()
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '订单已删除',
							icon: 'success'
						})
						this.checkType()
					}
				}
			})
		},
		//申请退款
		returnMoney() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/returnGoods",
				data: {
					token: uni.getStorageSync('token'),
					order_id: this.orders[this.modelIndex].order_id,
					type: 0
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '已申请退款',
							icon: 'success'
						})
						this.checkType()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//取消订单
		cancleOrder() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderCancel",
				data: {
					id: this.orders[this.modelIndex].order_id,
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.showToast({
							title: '订单已取消',
							icon: 'success',
							duration:1000
						})
						this.checkType()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//提醒发货
		remindSend(index) {
			this.showToast('该功能暂未开放')
			return
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/remindSend",
				data: {
					token: uni.getStorageSync('token'),
					order_id: this.orders[index].order_id,
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						this.showToast('已提醒商家尽快发货')
					}
				}
			})
		},
		//查看物流
		checkLogistics(orderid) {
			// this.showToast('该功能暂未开放')
			// return
			uni.navigateTo({
				url: '../expressInfo/expressInfo?orderid=' + orderid
			})
		},
		//确认收货
		sureGet() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/confirmGet",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orders[this.modelIndex].order_id
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.closeModel()
					if (res.data.msg == '操作成功') {
						// this.orders.splice(this.modelIndex, 1)
						uni.showToast({
							title: '已确认收货',
							icon: 'success'
						})
						this.checkType()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//显示操作确认弹窗
		showModel(index, str) {
			this.modelIndex = index
			this.tipTxt = str
			if (str == '删除订单') {
				this.showToast('该功能暂未开放')
				return
			}
			this.modelName = "showModel"
		},
		//点击弹窗里的确认
		sure() {
			console.log(this.tipTxt)
			switch (this.tipTxt) {
				case '取消订单':
					this.cancleOrder()
					break;
				case '删除订单':
					this.sureDel()
					break;
				case '申请退款':
					this.returnMoney()
					break;
				case '收货':
					this.sureGet()
					break;
				default:
					break;
			}
			this.closeModel()
		},
		//关闭弹窗
		closeModel() {
			this.modelName = ''
		},
		//跳转选择售后类型
		toAftersale(orderid) {
			uni.navigateTo({
				url: "../chooseAftersaleType/chooseAftersaleType?orderid=" + orderid
			})
		},
		//跳转查看订单详情
		toOrderDetail(index, orderid) {
			uni.navigateTo({
				url: "../orderDetail/orderDetail?orderid=" + orderid + "&aftersaleStatus=" + this.showBtn
			})
		}
	},
	//触底加载更多订单
	onReachBottom() {
		this.loadmore()
	},
	//下拉刷新
	onPullDownRefresh() {
		uni.startPullDownRefresh({
			success: () => {
				console.log('下拉刷新了页面')
				this.checkType()
				uni.stopPullDownRefresh()
			}
		})
	}
}
